/* Add this to your CSS file or style block */
.custom-container {
    min-height: 100vh; 
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
  }

  .btn-custum {
    border-radius: 30px;
  }
  .btn-form {
    border-radius: 30px;
  }

  .text-light-custom {
    font-family: 'Roboto', sans-serif;
    color: #c5c5c5 !important;
  }
    
  @media (max-width: 330px) {
    .btn-form {
      border-radius: 11px !important;
      padding: 3px;
      font-size: small;
      font-family: 'Roboto', sans-serif;
    }
  }
    
  @media (min-width: 330px) and (max-width: 391px)  {
    .btn-form {
      border-radius: 13px !important;
      padding: 3px 5px;
      font-size: smaller;
      font-family: 'Roboto', sans-serif;
    }
  }
  
  @media (min-width: 391px) {
    .btn-form {
      border-radius: 15px !important;
      padding: 4px 7px;
      font-family: 'Roboto', sans-serif;
    }
  }
  @media (min-width: 576px) {
    .custom-container {
      max-width: 540px;
    }
  }

  @media (min-width: 768px) {
    .custom-container {
      max-width: 720px;
    }
  }
  
  @media (min-width: 992px) {
    .custom-container {
      max-width: 960px;
    }
  }
  
  @media (min-width: 1200px) {
    .custom-container {
      max-width: 1140px;
    }
  }
  